/* src/index.css - modern dark theme with accents */
:root{
  --bg:#071427;
  --panel:#0b1630;
  --muted:#9fb0c9;
  --text:#e7f0fb;
  --accent:#d9b257;
  --glass: rgba(255,255,255,0.03);
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
}

*{box-sizing:border-box}
html,body,#root{height:100%}
body{margin:0;background:linear-gradient(180deg,var(--bg),#02101c);color:var(--text);-webkit-font-smoothing:antialiased}
.container{max-width:1100px;margin:0 auto;padding:28px}

.topbar{backdrop-filter: blur(6px);background:linear-gradient(180deg, rgba(255,255,255,0.02), transparent);border-bottom:1px solid rgba(255,255,255,0.03)}
.nav-inner{display:flex;align-items:center;justify-content:space-between;padding:12px 0}
.brand{color:var(--accent);font-weight:700;text-decoration:none;font-size:1.1rem}
.nav-links a{color:var(--muted);text-decoration:none;margin-left:14px}

.hero{padding:72px 0;text-align:center}
.hero-inner{padding:40px;background:linear-gradient(180deg, rgba(217,178,87,0.06), transparent);border-radius:12px}
.hero h1{font-size:3rem;margin:0;color:var(--accent);letter-spacing:0.6px}
.subtitle{color:var(--muted);margin-top:10px;font-weight:500}

.cta{display:inline-block;margin-top:18px;padding:10px 18px;border-radius:10px;background:var(--accent);color:#041426;font-weight:700;text-decoration:none;box-shadow:0 8px 30px rgba(217,178,87,0.12);transition:transform .18s}
.cta:hover{transform:translateY(-3px)}

.intro p{line-height:1.6;color:rgba(231,240,251,0.92);margin:12px 0}

.system{margin-top:28px}
.system h2{margin-bottom:12px}

.grid{display:grid;grid-template-columns:repeat(auto-fit,minmax(220px,1fr));gap:18px}
.card{
  display:block;
  background:linear-gradient(180deg,var(--panel), rgba(255,255,255,0.02));
  padding:18px;border-radius:12px;text-decoration:none;color:inherit;
  box-shadow: 0 6px 20px rgba(2,6,23,0.6);
  border:1px solid rgba(255,255,255,0.02);
  transition:transform .18s, box-shadow .18s, border-color .18s;
}
.card:hover{transform:translateY(-6px);box-shadow:0 18px 40px rgba(2,6,23,0.7);border-color:rgba(217,178,87,0.14)}
.card h3{margin:0 0 8px;color:var(--accent)}
.card p{margin:0;color:var(--muted)}

.page{padding:22px 0}
.back{display:inline-block;margin-bottom:14px;color:var(--muted);text-decoration:none}
.feature-grid{display:grid;grid-template-columns:repeat(auto-fit,minmax(240px,1fr));gap:14px;margin-top:18px}
.feature{background:var(--glass);padding:16px;border-radius:10px;border:1px solid rgba(255,255,255,0.02)}
.feature h3{margin:0 0 8px;color:var(--accent)}
.feature p{margin:0;color:var(--muted)}

.projects{display:grid;gap:12px;margin-top:16px}
.project-card{background:linear-gradient(180deg,var(--panel), rgba(255,255,255,0.02));padding:16px;border-radius:10px;border:1px solid rgba(255,255,255,0.02)}
.project-card h3{margin:0 0 6px}
.muted{color:var(--muted)}
.pill{display:inline-block;padding:6px 10px;border-radius:999px;font-weight:600;margin-top:10px}
.pill.done{background:rgba(58,160,80,0.12);color:#3aa050}
.pill.ongoing{background:rgba(217,178,87,0.08);color:var(--accent)}
.pill.planned{background:rgba(159,176,201,0.06);color:var(--muted)}

.footer{padding:28px 0;text-align:center;color:var(--muted);border-top:1px solid rgba(255,255,255,0.02);margin-top:36px}

/* responsive tweaks */
@media (max-width:640px){
  .hero h1{font-size:2.2rem}
  .container{padding:18px}
  .cta{padding:10px 14px}
}
